html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  width: 100%;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
  color: #333;
}

a {
  display: block;
  text-decoration: none;
  color: #333;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor--max {
  width: 80%;
}
@media (min-width: 768px) {
  .contenedor--max {
    width: 70%;
  }
}

.header__nav {
  position: relative;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .header__nav {
    margin-bottom: 4.2rem;
  }
}
.header__name {
  font-size: 3.6rem;
  font-weight: 800;
  margin: 0;
}
.header__menu {
  cursor: pointer;
  z-index: 20;
  position: absolute;
  visibility: visible;
  opacity: 1;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .header__menu {
    visibility: hidden;
    opacity: 0;
  }
}
.header__menu p {
  margin: 0;
}
.header__nav {
  display: flex;
  justify-content: space-between;
}
.header__links {
  position: absolute;
  left: 200%;
  transform: translate(-50%, 35vh);
  display: flex;
  flex-direction: column;
  font-weight: 500;
  gap: 2rem;
  text-align: center;
  z-index: 30;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .header__links {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
  }
}
.header__link {
  font-size: 2.2rem;
  color: #FFF;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  background-color: rgb(78, 88, 97);
  left: 100%;
  transition: all 0.3s ease;
  z-index: 10;
  opacity: 0;
}

.ac-overlay {
  left: 0;
  opacity: 1;
}

.li-active {
  left: 50%;
}

.hamburger {
  padding: 0;
}
.hamburger.is-active .hamburger-inner {
  background-color: #FFF;
  transition: all 0.3s;
}
.hamburger.is-active .hamburger-inner::after, .hamburger.is-active .hamburger-inner::before {
  background-color: #FFF;
  transition: all 0.3s;
}
.hamburger .hamburger-inner {
  background-color: #333;
  transition: all 0.3s;
}
.hamburger .hamburger-inner::after, .hamburger .hamburger-inner::before {
  background-color: #333;
  transition: all 0.3s;
}

.hero__cabecera {
  padding-right: 1rem;
}
@media (min-width: 768px) {
  .hero__cabecera {
    padding-right: 25rem;
  }
}
.hero__anotation {
  color: #2D9CDB;
  font-size: 1.2rem;
  margin: 0.5rem 0;
}
@media (min-width: 768px) {
  .hero__anotation {
    font-size: 1.8rem;
  }
}
.hero__title {
  font-size: 2.4rem;
  margin-bottom: 1.5rem;
  font-weight: 500;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 4.8rem;
  }
}
.hero__picture {
  border-radius: 1.8rem;
}