@use 'base/mixins' as m;
@use 'base/variables' as v;

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*,*:before,*:after {
    box-sizing: inherit;
}

body {
    font-size: 1.6rem;
    width: 100%;
    min-height: 100vh;
    font-family: v.$fuente;
    overflow-x: hidden;
    color: #333;
}

a {
    display: block;
    text-decoration: none;
    color: #333;
}

img {
    max-width: 100%;
    display: block;
}

h1,h2,h3 {
    margin: 0;
}


h1 {
    font-size: 5rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3rem;
}

.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}

.contenedor--max {
    width: 80%;
    @include m.tablet {
        width: 70%;
    }
}


